import React, { Component } from "react";

class Home extends Component {
  render() {
    return (
      <div
        className="d-flex flex-md-row flex-column justify-content-center align-items-center"
        style={{ minHeight: "50vh" }}
      >
        <div className="p-2">
          <img
            style={{
              borderRadius: "50%",
              boxShadow: "1px 1px 14px -3px rgba(0, 0, 0, 0.75)",
            }}
            src={process.env.PUBLIC_URL + "img/me2.png"}
            alt="me"
          />
        </div>
        <div className="p-2">
          <h1 style={{ fontWeight: "900", textAlign: "center" }}>
            Matheus Teixeira
          </h1>
          <h4 style={{ textAlign: "center" }}>Programador</h4>
          <p
            className="mt-4"
            style={{ fontWeight: "900", textAlign: "center" }}
          >
            <a className="highlight" href="https://github.com/matheustalves">
              <i className="fab fa-github"></i> matheustalves
            </a>
          </p>
          <p style={{ fontWeight: "900", textAlign: "center" }}>
            <a className="highlight" href="mailto:matheustalves@outlook.com">
              <i className="fas fa-envelope"></i> matheustalves@outlook.com
            </a>
          </p>
        </div>
      </div>
    );
  }
}

export default Home;
