import React, { Component } from "react";

class Skills extends Component {
  render() {
    return (
      <div className="container-fluid justify-content-center align-items-center mt-5" style={{minHeight: "50vh"}}>
        <div className="row">
          <div className="col col-6 col-sm-6 col-md-3 my-3">
            <i
              className="fab fa-python fa-7x"
              title="Python/Django"
            ></i>
          </div>
          <div className="col col-6 col-sm-6 col-md-3 my-3">
            <i className="fab fa-cuttlefish fa-7x" title="C/C++"></i>
          </div>
          <div className="col col-6 col-sm-6 col-md-3 my-3">
            <i className="fab fa-java fa-7x" title="Java"></i>
          </div>
          <div className="col col-6 col-sm-6 col-md-3 my-3">
            <i className="fab fa-js-square fa-7x" title="JavaScript"></i>
          </div>
        </div>
        <div className="row justify-content-center align-items-center">
          <div className="col col-6 col-sm-6 col-md-3 my-3">
            <i className="fab fa-html5 fa-7x" title="HTML5"></i>
          </div>
          <div className="col col-6 col-sm-6 col-md-3 my-3">
            <i className="fab fa-react fa-7x" title="React"></i>
          </div>
          <div className="col col-6 col-sm-6 col-md-3 my-3">
            <i className="fab fa-node fa-7x" title="Node.JS"></i>
          </div>
          <div className="col col-6 col-sm-6 col-md-3 my-3">
            <i className="fab fa-css3-alt fa-7x" title="CSS3"></i>
          </div>
        </div>
        <div className="row justify-content-center align-items-center">
          <div className="col col-6 col-sm-6 col-md-3 my-3">
            <i className="fab fa-git fa-7x" title="Git/GitHub"></i>
          </div>
          <div className="col col-6 col-sm-6 col-md-3 my-3">
            <i className="fas fa-database fa-7x" title="DBs SQL/NoSQL"></i>
          </div>
          <div className="col col-6 col-sm-6 col-md-3 my-3">
            <i className="fab fa-linux fa-7x" title="Linux"></i>
          </div>
          <div className="col col-6 col-sm-6 col-md-3 my-3">
            <i className="fas fa-microchip fa-7x" title="Hardware/Manutenção"></i>
          </div>
        </div>
      </div>
    );
  }
}

export default Skills;
