import React, { Component } from "react";

class Projects extends Component {
  render() {
    return (
      <div className="container-fluid justify-content-center align-items-center">
        <div className="row">
          <div className="col col-lg-6 col-md-12 col-sm-12 col-12 mt-3">
            <h4
              className="font-weight-bold"
              style={{ fontFamily: "DM Serif Text" }}
            >
              Dataset Enhancer
            </h4>
            <p>
              Aplicação que usa Data Augmentation para gerar novas imagens para
              datasets de Redes Neurais.
            </p>
            <div className="text-wrap text-break font-weight-bold mb-3">
              <span style={{ color: "#5caee6" }}>Python</span>
              <span className="mx-2">|</span>
              <span style={{ color: "#35b854" }}>Django</span>
              <span className="mx-2">|</span>
              <span style={{ color: "#f8871c" }}>TensorFlow</span>
              <span className="mx-2">|</span>
              <span style={{ color: "#c23ab9" }}>Web Stack</span>
              <span className="mx-2">|</span>
              <span style={{ color: "#5be9e9" }}>Git</span>
            </div>
            <img
              src={process.env.PUBLIC_URL + "img/de.gif"}
              className="img-fluid"
              alt="DatasetEnhancer"
            ></img>
            <p className="mt-3">
              <a
                href="https://datasetenhancer.pythonanywhere.com"
                target="_blank"
              >
                <i className="fas fa-link fa-3x" style={{ color: "black" }}></i>
              </a>
            </p>
          </div>

          <div className="col col-lg-6 col-md-12 col-sm-12 col-12 mt-3">
            <h4
              className="font-weight-bold"
              style={{ fontFamily: "DM Serif Text" }}
            >
              BOT com Rede Convolucional
            </h4>
            <p>
              BOT para RPG Online (Runescape) usando Rede Convolucional e
              Algoritmos de Visão Computacional para identificação de objetos.
            </p>
            <div className="text-wrap text-break font-weight-bold mb-3">
              <span style={{ color: "#5caee6" }}>Python </span>
              <span className="mx-2">|</span>
              <span style={{ color: "#f8871c" }}>Pytorch </span>
              <span className="mx-2">|</span>
              <span style={{ color: "#d9db34" }}>Numpy </span>
              <span className="mx-2">|</span>
              <span style={{ color: "#3ee239" }}>OpenCV </span>
              <span className="mx-2">|</span>
              <span style={{ color: "#5be9e9" }}>Git </span>
            </div>
            <img
              src={process.env.PUBLIC_URL + "img/bot.gif"}
              className="img-fluid"
              alt="Bot"
            ></img>

            <p className="mt-3">
              <a
                href="https://github.com/matheustalves/rs-netbot"
                target="_blank"
              >
                <i
                  className="fab fa-github fa-3x"
                  style={{ color: "black" }}
                ></i>
              </a>
            </p>
          </div>
        </div>
      </div>
    );
  }
}

export default Projects;
